body{
    font-family: sans-serif;
    font-size: small;
    background-color: aqua;
}
.main{
    height: auto;
    width: auto;
}
h2{
    text-align: center;
    background-color: purple;
    color: white;
    padding: 15px;
    width: 500px;
   margin: 100px auto;
   margin-bottom: 0px;
   box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}
.data{
    width: 100%;
    background-color: whitesmoke;
    padding: 10px 20px;
    box-sizing: border-box;
    cursor: pointer;
}
.dustbin{
    float: right;
   
}
.data:hover {
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
    
}
.con{
    background-color: white;
    width: 530px;
    height: auto;
    display: none;
    margin: 30px auto;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}
button{
    border: none;
    padding: 15px 30px;
     background-color: purple;
     font-size: 25px;
     color: white;
     box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
     margin-top: 20px;
     cursor: pointer;
     outline: none;
     position: absolute;
     left: 50%;
     -ms-transform: translate(-50%, -50%);
     transform: translate(-50%, -50%);
     border-radius: 54px;
}
button:hover{
    opacity: 0.8;
}
.search{
    background-color: white;
    width: 530px;
   visibility: hidden;
   max-height: 0;
    height: auto;
    margin: 15px auto;
    
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}
input{
    width: 95%;
    box-sizing: border-box;
    border: none;
    height: 30px;
    padding: 20px;
    outline: none;
}
